/* global Game */
'use strict';

var harvester = require('harvester');
var guard = require('guard');
var builder = require('builder');
var test = require('test');
var autospawn = require('autospawn');

module.exports.loop = function () {

  autospawn();

  for (var _name in Game.creeps) {
    if (_name) {
      var creep = Game.creeps[_name];

      if (creep.memory.role === 'harvester') {
        harvester(creep);
      }

      if (creep.memory.role === 'builder') {
        builder(creep);
      }

      if (creep.memory.role === 'guard') {
        guard(creep);
      }

      if (creep.memory.role === 'test') {
        test(creep);
      }
    }
  }
};
/* global Game, MOVE, WORK, CARRY, ATTACK */
'use strict';

module.exports = function () {

  // only generate if larger than 200 energy
  if (Game.spawns.Spawn1.energy < 200 || !!Game.spawns.Spawn1.spawning) {
    return;
  }

  // Generate these creeps
  var creeps = {
    harvester: {
      desired: 4,
      create: function create() {
        Game.spawns.Spawn1.createCreep([MOVE, WORK, CARRY], null, { role: 'harvester' });
      }
    },
    builder: {
      desired: 2,
      create: function create() {
        Game.spawns.Spawn1.createCreep([MOVE, WORK, CARRY], null, { role: 'builder' });
      }
    },
    guard: {
      desired: 4,
      create: function create() {
        Game.spawns.Spawn1.createCreep([MOVE, WORK, CARRY, ATTACK], null, { role: 'guard' });
      }
    },
    test: {
      desired: 1,
      create: function create() {
        Game.spawns.Spawn1.createCreep([MOVE, WORK, CARRY], null, { role: 'test' });
      }
    }
  };

  function mustGenerate(creep, role) {
    var active = 0;

    // collect roles
    for (var i in Game.creeps) {
      if (role === Game.creeps[i].memory.role) {
        active = active + 1;
      }
    }
    return active < creep.desired;
  }

  for (var role in creeps) {
    if (mustGenerate(creeps[role], role)) {
      void 0;
      if (creeps[role] && typeof creeps[role].create === 'function') {
        creeps[role].create();
        return; // done
      }
    }
  }
};
/* global Game, ERR_NOT_IN_RANGE, FIND_CONSTRUCTION_SITES, STRUCTURE_ROAD */
'use strict';

module.exports = function (creep) {

  // only build when having more than 200 energy
  if (Game.spawns.Spawn1.energy < 201 || !!Game.spawns.Spawn1.spawning) {
    return;
  }

  // collect energy
  if (creep.carry.energy === 0) {
    if (creep.carry.energy <= creep.carryCapacity && Game.spawns.Spawn1.transferEnergy(creep) === ERR_NOT_IN_RANGE) {
      creep.say('gather resources');
      creep.moveTo(Game.spawns.Spawn1);
    }
    return;
  }

  // build construction
  var targets = creep.room.find(FIND_CONSTRUCTION_SITES);
  if (targets.length) {
    creep.say('building');
    if (creep.build(targets[0]) === ERR_NOT_IN_RANGE) {
      creep.moveTo(targets[0]);
    }
    return;
  }

  // upgrade controller
  if (creep.room.controller) {
    creep.say('upgrade controller');
    if (creep.upgradeController(creep.room.controller) === ERR_NOT_IN_RANGE) {
      creep.room.createConstructionSite(creep, STRUCTURE_ROAD); // build road to the controller
      creep.moveTo(creep.room.controller);
    }
    return;
  }
};
/* global Game, FIND_HOSTILE_CREEPS, ERR_NOT_IN_RANGE */
"use strict";

module.exports = function (creep) {
  var targets = creep.room.find(FIND_HOSTILE_CREEPS);
  if (targets.length) {
    if (creep.attack(targets[0]) === ERR_NOT_IN_RANGE) {
      creep.moveTo(targets[0]);
    }
  } else {
    if (Game.flags.Flag1) {
      creep.moveTo(creep.room.find(Game.flags.Flag1));
    }
  }
};
/* global Game, ERR_NOT_IN_RANGE, FIND_SOURCES */
"use strict";

module.exports = function (creep) {
  if (creep.carry.energy < creep.carryCapacity) {
    var sources = creep.room.find(FIND_SOURCES);
    if (creep.harvest(sources[0]) === ERR_NOT_IN_RANGE) {
      creep.moveTo(sources[0]);
    }
  } else if (creep.transferEnergy(Game.spawns.Spawn1) === ERR_NOT_IN_RANGE) {
    creep.moveTo(Game.spawns.Spawn1);
  }
};
